// Note: Faux Columns is in _grid.scss

// @TODO: fix for smaller sizes

.sidebar {
	line-height: 1.2;
	padding: 0;

	ul,
	ol { // @TODO OL is for on page scroll_nav_list only. Bloating CSS. Fix.
		padding: 0;
		/* top level nav items */
		> li {
			position: relative;

			> a {
				display: block;
				font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.02em;
				text-transform: uppercase;
				width: 100%;
			}

		}

		//
		// Main Site Left Nav
		//


		&.main-ocs-navigation {

			background-color: $greyDark;


			// generate sprite positions
			@for $i from 0 through 9 {
				>li:nth-child(#{$i+1}) > a:before
				{
					background: url($nav-sprite) 0 $i*-62px no-repeat;
				}
				//>li:nth-child(#{$i+1}) > a:hover:before,
				/*>li:nth-child(#{$i+1}).active > a:before,*/
				>li:nth-child(#{$i+1}).current_page_item > a:before,
				>li:nth-child(#{$i+1}).current_page_parent > a:before,
				>li:nth-child(#{$i+1}).current_page_ancestor > a:before
				{
					background: url($nav-sprite) -62px $i*-62px no-repeat;
				}

			}
			> li {
				/* for vertical centering on the <a>s */
				display: table;
				min-height: 60px;
				width: 100%;

				//
				// Sub-Pages
				//
				> ul, > ul ul {
					display: none;
					background-color: $greyLighter;
						> li {
							/*margin: 3px -15px;*/

							> a {
								color: $greyDark;
								font-family: $headings-font-family-lighter;
								font-weight: normal;
								/*padding: 10px $sidebarPadding;*/
								padding: 10px 7px 10px 18px;
							}
							&:hover > a { //put the :hover on the LI so it stays highlighted when in sub menu flyouts
								background-color: mix($grey, $greyLighter);
								color: mix($greyDark, #000000);
								text-decoration: none;
							}
							/*&.current_page_parent > a,*/
							/*&.current_page_ancestor > a, {*/
							&.current_page_item > a {
								background-color: #fff;
								border-left: 16px solid #d6d7d8;
								color: mix($greyDark, #000000);
								text-decoration: none;
							}
						}
				}
				> ul.children ul.children {
					a { padding-left: 30px; font-size: 12px; }
				}
				// Show it
				&.current_page_item,
				&.current_page_parent,
				&.current_page_ancestor {
					> ul, > ul ul {
						display: table-row;
					}
				}
				// Show it on hover
				// But not if it's already open.
				&.page_item_has_children:not(.current_page_item):not(.current_page_parent):not(.current_page_ancestor):hover {
					position: relative;

					ul {
						background-color: $greyDark;
						display: block;
						position: absolute;
						left: 100%;
						top: 1px;
						width: 250px;
						z-index: 1111;

						// SUB SUB nav, ONS mostly
						> .page_item_has_children {

							> a:after {
								content: "";
								font-family: serif;
								font-size: 17px;
								font-weight: normal;
								position: absolute;
								right: 6px;
								top: 8px;
							}
							> ul { display: none;}
							&:hover > ul { display: block;}
							.children a { padding-left: 18px; font-size: 14px; }


						}

						a {
							color: #ffffff;
						}
					}
				}

				&.current_page_item > a,
				&.current_page_parent > a,
				&.current_page_ancestor > a,
				// make sure we don't go dark on hover
				&.current_page_item:hover > a,
				&.current_page_parent:hover > a,
				&.current_page_ancestor:hover > a,
				&.active > a {
					background-color: #fff;
					color: #000000;
				}
				&:hover > a { //put the :hover on the LI so it stays highlighted when in sub menu flyouts
					background-color: darken($greyDark, 10%);
					text-decoration: none;
				}
				> a {
					/*background: url(../images/icons-nav@2x.png) 0 -4px no-repeat;*/
					color: #fff;
					display: table-cell;
					font-family: $headings-font-family;
					height: 60px;
					padding: 14px 14px 14px 60px;
					vertical-align: middle;
				}

				> a:before {
					content: " ";
					display: block;
					height: 62px;
					margin-left: -62px;
					position: absolute;
					top: -4px;
					width: 62px;
				}

			}

		}


		//
		// Right Side Navigation
		//

		&.sub-page-navigation, &.scroll-nav__list {

			background-color: $greyLighter;

			> li {
				/*margin: 3px -15px;*/

				> a {
					color: $greyDark;
					font-family: $headings-font-family-lighter;
					font-weight: normal;
					padding: 10px $sidebarPadding;
				}
				&.current_page_item > a,
				> a:hover {
					background-color: $grey;
					color: #fff;
					text-decoration: none;
				}
			}
			.children {
				display: none;
			}
			>.current_page_item .children {
				display: block;
			}
			.sub-menu a, .children a {
				color: $grey;
				display: block;
				font-family: $headings-font-family-lighter;
				min-height: auto;
				padding: 6px 8px 8px 24px;
				/*text-transform: none;*/
				font-size: 14px;
			}
			.active .sub-menu { display: table-row; }
		}

		/*
		&.scroll-nav__list {
			> li {
				&.current_page_item > a,
				> a:hover {
					background-color: $greyLight;
				}
			}
		}
		*/

	}

	ul ul {
		margin: 0;
		padding: 0;
	}

	li {
		list-style-type: none;
		margin: 0;
		padding: 0;
	}



	.active a {
		background-color: $greyLight;
	}

	> .brand {
		background-color: $greenDark;
		display: block;
		height: $footerHeight;
		width: 100%;
		//hide text
		font: 0/0 a;
		color: transparent;

		@include img-retina("../images/logo-odfw-with-text_sm.png", "../images/logo-odfw-with-text_sm@2x.png", 145px, 50px);
		background-position: 50% 50%;
		background-repeat: no-repeat;

		@include breakpoint (tablet) {
			@include make-sm-column($sidebar-sm-columns);
			position: fixed;
			bottom: 0;
			left: 0;
			z-index: 5555;
			/*width: 16.6667%;*/
		}

	}

}

.inner-sidebar {
	background-color: $greyLighter;

	h2, .scroll-nav__heading {
		padding-left: $sidebarPadding;
	}
}
.sub-page-navigation+.on-page-nav {
	border-top: 1px solid $greyLight;
	padding-top: $basePadding;
	margin-top: $basePadding;
}
.scroll-nav__heading {
	@extend h2;
	display: block;
}
/*.scroll-nav.fixed .scroll-nav__wrapper {*/
.scroll-nav.fixed {
  position: fixed;
  top: 60px;
  width: inherit;
}
