/*moved all body compass/map-related 'stuff' here - hopefully more coherent*/

body {
    button.view-map,
    .compass { 
        display: none;
    }
    main { display: block; }

    &.map-visible {
        main { display: none; }
        .compass { 
            display: block;
            position: relative;
        }
    }

    &.map-available {
       button.view-map { display: block;} 
    }
}

.compass.main {
    min-height: 0px;
    height: calc(100vh - 138px);
    background-color: #fff;
}

.compass-container,
.compass-wrap,
.compass-iframe {
    height: 100%;
    width: 100%;
}

.compass-container {
    position: absolute;
}

.compass-wrap {
    position: relative;
    /*padding-top: 35px; //for any chrome*/
    overflow: hidden;

    > iframe {
        position: absolute;
        top:0;
        left: 0;
    }
}

.compass-close {
    position: absolute;
    top: 25px;
    right: 15px;
    cursor: pointer;
    z-index: 1000;

    > .glyphicon {
        font-size: 3.25em;
        color: $grey;

        &:hover {
            color: $greyDark;
        }
    }
}

.view-external-compass {
    position: absolute;
    height: 50px;
    top: 20px;
    right: 75px;
    font-family: $headings-font-family-lighter;
    font-size: 18px;
    border: 1.5px solid $brand-primary;
    background-color: $off-white;
    padding: 6px;
    z-index: 1000;
    -moz-box-shadow:    1.5px 1.5px 1px 1.5px $greyDark;
    -webkit-box-shadow: 1.5px 1.5px 1px 1.5px $greyDark;
    box-shadow:         1.5px 1.5px 1px 1.5px $greyDark;

    &:hover {
        background-color: $greyLighter;
    }

    a {
        color: $greyDark;
        text-decoration: none;
    }
   

/*    .glyphicon {
        color: $link-color;
    }
*/
    span.compass-icon {
        background: url($nav-sprite) no-repeat;
        background-position: -78px -888px;
        padding-left: 40px;
        line-height: 35px;
        display: inline-block;
        margin: auto;
    }
}